<template>
  <!-- Facebook -->
  <div class="weibo_list_main page_main">
    <ul>
      <!--<li v-for="item in data">-->
        <!--<div class="header_img"><img class="img" :src='item.avatarUrl'></div>-->
        <!--<dl>-->
          <!--<dt class="name_title">{{ item.userName }}</dt>-->
          <!--<dd v-for="(k, v) in Object.keys(item)"><span>{{ translates[k] }}: </span>{{ item[k] }}</dd>-->
        <!--</dl>-->
        <!--<div v-for="(k, v) in Object.keys(item)">-->
          <!--<p style="margin: 10px"> {{ k }} -　{{ translates }}－　{{ v }}</p>-->
        <!--</div>-->
      <!--</li>-->
      <li v-for="item in data">
        <div class="header_img"><img class="img" :src='item.avatarUrl'/></div>
        <dl>
          <dt class="name_title">{{ item.userName }}</dt>
          <dd><span>ID: </span>{{ item.id }}</dd>
          <dd><span>性别: </span>{{ item.gender }}</dd>
          <dd><span>关注数: </span>{{ item.followCount }}</dd>
          <dd><span>粉丝数: </span>{{ item.fansCount }}</dd>
          <dd><span>观点数: </span>{{ item.viewCount }}</dd>
          <dd><span>账号类型: </span>{{ item.idType }}</dd>
          <dd><span>生日: </span>{{ item.birthday }}</dd>
          <dd><span>朋友数: </span>{{ item.friendCount }}</dd>
          <dd><span>发文数: </span>{{ item.postCount }}</dd>
          <dd><span>点赞数: </span>{{ item.likeCount }}</dd>
          <dd><span>教育经历: </span>{{ item.educations }}</dd>
          <dd><span>工作组织: </span>{{ item.employer }}</dd>
          <dd><span>位置: </span>{{ item.location }}</dd>
          <dd><span>学校名称: </span>{{ item.schoolName }}</dd>
          <dd><span>工作经验: </span>{{ item.works }}</dd>
          <dd><span>网址: </span><a target="_blank" :href="item.url">{{ item.url }}</a></dd>
        </dl>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'facebook',
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.FACEBOOK
      },
      searchKey: {
        type: String,
        default: '成都'
      }
    },
    data () {
      return {
        data: [],
        translates: {}
      }
    },
    created () {
      gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
        .then(res => {
          // console.log('commonSearchApi:' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          console.log('Facebook111111', this.resData)
          console.log('Facebook222222', this.data[0])
          console.log('Facebook333333', this.translates)
        })
    },
    methods: {
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .weibo_list_main {
    overflow: hidden;
  }
  .weibo_list_main ul {
    overflow: hidden;
  }
  .weibo_list_main ul li {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 0 rgba(6,0,1,0.1);
    margin: 15px 10px;
    padding: 20px;
    position: relative;
  }
  .weibo_list_main ul li .header_img {
    position: absolute;
    top: 30px;
    left: 20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  .header_img .img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: block;
  }
  .weibo_list_main ul li dl {
    display: inline-block;
    vertical-align: middle;
    margin-left: 150px;
  }
  .weibo_list_main ul li dl .name_title {
    color: #666666;
    font-size: 20px;
    margin-bottom: 20px;
  }
  .weibo_list_main ul li dl dd {
    margin-bottom: 20px;
    font-size: 16px;
    color: #666666;
    float: left;
    min-width: 300px;
  }
  .weibo_list_main ul li dl dd span {
    color: #909090;
    font-size: 16px;
    margin-right: 6px;
  }
</style>



