<template>
  <!-- 微博列表 -->
  <div class="weibo_list_main page_main">
    <ul>

      <li v-for="item in data" @click="showDetail(item.id)">
        <div class="header_img"><img class="img" :src="item.avatarUrl" alt=""></div>
        <dl>
          <dt class="name_title">{{ item.userName }}</dt>
          <dd><span>简介:</span>{{ item.biography }}</dd>
          <dd><span>性别:</span>{{ item.gender === 'm' ? '男' : '女' }}</dd>
          <dd><span>地区:</span>{{ item.location }}</dd>
          <button @click="clicked(item)">点击详细: {{ item.id }}</button>
        </dl>
      </li>

    </ul>
  </div>
</template>

<script>
  import bus from '../common/bus'

  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.WEIBO
      },
      searchKey: {
        type: String,
        default: '成都'
      }
    },
    data () {
      return {
        data: [],
        translates: {}
      }
    },
    methods: {
      showDetail (id) {
        bus.$emit('createNewTag', {
          searchType: gconst.SearchType.EX_WEIBO_DETAIL,
          searchKey: id
        })
      }
    },
    created () {
      gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
        .then(res => {
          // console.log('commonSearchApi:' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
        })
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .weibo_list_main {
    /*overflow: hidden;*/
    height: 100%;
  }
  .weibo_list_main ul {
    overflow: hidden;
    display: flex;
    display: -webkit-flex; /* Safari */
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .weibo_list_main ul li {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 0 rgba(6,0,1,0.1);
    margin: 15px 10px;
    padding: 20px;
    max-width: 480px;
  }
  .weibo_list_main ul li .header_img {
    display: inline-block;
    vertical-align: top;
    background: #3F4262;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 30px;
  }

  .img {
    width: 100px;
    height: 100px;
  }

  .weibo_list_main ul li dl {
    display: inline-block;
    vertical-align: middle;
    max-width: 345px;
  }
  .weibo_list_main ul li dl .name_title {
    color: #666666;
    font-size: 20px;
    margin-bottom: 20px;
  }
  .weibo_list_main ul li dl dd {
    margin-bottom: 20px;
    font-size: 16px;
    color: #666666;
  }
  .weibo_list_main ul li dl dd span {
    color: #909090;
    font-size: 16px;
  }
</style>



