<!--
通用: 显示结果table部分
通过seq来控制所有的显示
-->
<template>
 <span class="my_container">
   <span :class="[ enableLink ? 'enable_interactive' : '' , 'linkValue', 'value_attr']">{{ v }}</span>
   <img v-show='enableLink' class="img_search" src="../../assets/images/search/search.png" @click="jump">
 </span>
</template>

<script>
  export default {
    props: {
      k: {
        type: String,
      },
      v: {
        type: String,
      },
      fix: {      // 修正序列
        type: Object,
        'test': {
          linkType: gconst.LinkType.NO_LINK
        }
      }
    },
    data: function () {
      return {
        seq: ['id', 'ip'],
        translates: {}
      }
    },
    methods: {
      jump() {
        alert("jump:" +this.fix[this.k].linkType)
      }
    },
    computed: {
      enableLink () {
        if (this.fix && this.fix[this.k]) {
          let linkType = this.fix[this.k].linkType
          if (linkType)
            return true
        }

        return false
      }
    },
    created () {

    }
  }
</script>


<style>
  @import "../../../static/css/main.css";
  @import "../search.css";

  .my_container {
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
  }

  .linkValue {
    display: inline;
  }

  .img_search {
    /*background: red;*/
    display: inline;
    margin-left: 5px;
    width: 20px;
    height: 20px;
  }

</style>



