      // QQ基本信息
      localqqdata () {
        let params = {
          rowNumPerPage: 5,
          query: this.searchKey,
          scrollId: this.scrollId
        }
        gapi.getLabelsqqdata(params)
          .then(res => {
            console.log('QQ基本信息', res)
            if (res.data.code === 1) {
              let headerLists = res.data.data.headers
              let valueLists = res.data.data.resultList
              let a = [] //  header里面所有的 英文key
              let b = [] //  数据里面所有的 英文key
              let c = [] //  需要渲染的 英文key
              let newObj = {} //  请求到的数据对象
              let newRenderingData = {}
              let btnObj = {} //  处理过的数据的标题对象
              this.qqdataObjData = {}
              /*
              *  data headers 数据处理
              * */
              for (let list in headerLists) {
                a.push(headerLists[list].fieldEN)
                btnObj[headerLists[list].fieldEN] = headerLists[list].fieldCH
              }
              /*
              *  data resultList 结果数据处理
              * */
              for (let list in valueLists) {
                newObj = valueLists[list]
                for (let key in valueLists[list]) {
                  b.push(key)
                }
              }
              // 数组取合集
              for (let i = 0; i < a.length; i++) {
                let temp = a[i]
                for (let j = 0; j < b.length; j++) {
                  if (temp === b[j]) {
                    c.push(temp)
                    break
                  }
                }
              }
              for (var i in c) {
                newRenderingData[c[i]] = newObj[c[i]]
              }
              /* 切换小标题的中英文 */
              for (var key in newRenderingData) {
                this.qqdataObjData[btnObj[key]] = newRenderingData[key]
              }
            } else {
              document.getElementById('qqdata').setAttribute('style', 'display: none')
            }
          })
          .catch(err => {
            document.getElementById('qqdata').setAttribute('style', 'display: none')
          })
