              let btnObj = {} //  处理过的数据的标题对象
              this.socialsecurityObjData = {}
              /*
              *  data headers 数据处理
              * */
              for (let list in headerLists) {
                a.push(headerLists[list].fieldEN)
                btnObj[headerLists[list].fieldEN] = headerLists[list].fieldCH
              }
              /*
              *  data resultList 结果数据处理
              * */
              for (let list in valueLists) {
                newObj = valueLists[list]
                for (let key in valueLists[list]) {
                  b.push(key)
                }
              }
              // 数组取合集
              for (let i = 0; i < a.length; i++) {
                let temp = a[i]
                for (let j = 0; j < b.length; j++) {
                  if (temp === b[j]) {
                    c.push(temp)
                    break
                  }
                }
              }
              for (var i in c) {
                newRenderingData[c[i]] = newObj[c[i]]
              }
              /* 切换小标题的中英文 */
              for (var key in newRenderingData) {
                this.socialsecurityObjData[btnObj[key]] = newRenderingData[key]
              }
            } else {
              document.getElementById('socialsecurity').setAttribute('style', 'display: none')
            }
          } else {
            document.getElementById('socialsecurity').setAttribute('style', 'display: none')
          }
        })
          .catch(err => {
            document.getElementById('socialsecurity').setAttribute('style', 'display: none')
          })
      },
      // 简历信息
      localresume () {
        let params = {
          rowNumPerPage: 5,
          query: this.searchKey,
          scrollId: this.scrollId
        }
        gapi.getLabelsresume(params).then(res => {
          console.log('简历信息', res)
          if (res.data.code === 1) {
            let headerLists = res.data.data.headers
            let valueLists = res.data.data.resultList
            let a = [] //  header里面所有的 英文key
            let b = [] //  数据里面所有的 英文key
            let c = [] //  需要渲染的 英文key
            let newObj = {} //  请求到的数据对象
            let newRenderingData = {}
            let btnObj = {} //  处理过的数据的标题对象
            this.resumeObjData = {}
            /*
            *  data headers 数据处理
            * */
            for (let list in headerLists) {
              a.push(headerLists[list].fieldEN)
              btnObj[headerLists[list].fieldEN] = headerLists[list].fieldCH
            }
            /*
            *  data resultList 结果数据处理
            * */
            for (let list in valueLists) {
              newObj = valueLists[list]
              for (let key in valueLists[list]) {
                b.push(key)
              }
            }
            // 数组取合集
            for (let i = 0; i < a.length; i++) {
              let temp = a[i]
              for (let j = 0; j < b.length; j++) {
                if (temp === b[j]) {
                  c.push(temp)
                  break
                }
              }
            }
            for (var i in c) {
              newRenderingData[c[i]] = newObj[c[i]]
            }
            /* 切换小标题的中英文 */
            for (var key in newRenderingData) {
              this.resumeObjData[btnObj[key]] = newRenderingData[key]
            }
          } else {
            document.getElementById('resume').setAttribute('style', 'display: none')
          }
        })
          .catch(err => {
            document.getElementById('resume').setAttribute('style', 'display: none')
          })
      },
      // 老幼病残孕信息
      localoldweakyoung () {
        let params = {
          rowNumPerPage: 5,
          query: this.searchKey,
          scrollId: this.scrollId
        }
        gapi.getLabelsoldweakyoung(params).then(res => {
          console.log('老幼病残孕信息', res)
          if (res.data.code === 1) {
            let headerLists = res.data.data.headers
            let valueLists = res.data.data.resultList
            if (headerLists.length !== 0 && valueLists.length !== 0) {
              let a = [] //  header里面所有的 英文key
              let b = [] //  数据里面所有的 英文key
              let c = [] //  需要渲染的 英文key
              let newObj = {} //  请求到的数据对象
              let newRenderingData = {}
              let btnObj = {} //  处理过的数据的标题对象
              this.oldweakyoungObjData = {}
              /*
              *  data headers 数据处理
              * */
              for (let list in headerLists) {
                a.push(headerLists[list].fieldEN)
                btnObj[headerLists[list].fieldEN] = headerLists[list].fieldCH
              }
              /*
              *  data resultList 结果数据处理
              * */
              for (let list in valueLists) {
                newObj = valueLists[list]
                for (let key in valueLists[list]) {
                  b.push(key)
                }
              }
              // 数组取合集
              for (let i = 0; i < a.length; i++) {
                let temp = a[i]
                for (let j = 0; j < b.length; j++) {
                  if (temp === b[j]) {
                    c.push(temp)
                    break
                  }
                }
              }
              for (var i in c) {
                newRenderingData[c[i]] = newObj[c[i]]
              }
              /* 切换小标题的中英文 */
              for (var key in newRenderingData) {
                this.oldweakyoungObjData[btnObj[key]] = newRenderingData[key]
              }
            } else {
              document.getElementById('oldweakyoung').setAttribute('style', 'display: none')
            }
          } else {
            document.getElementById('oldweakyoung').setAttribute('style', 'display: none')
          }
        })
          .catch(err => {
            document.getElementById('oldweakyoung').setAttribute('style', 'display: none')
          })
      }
    },
    components: {
      vSidebar
    }
  }
</script>

<style scoped>
  .section_main {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
  }
  .local_query {
    height: 100%;
    position: absolute;
    top: 0;
    left: 200px;
    overflow-x: auto;
    overflow-y: scroll;
    background-color: #F0F0F0;
    padding: 40px;
    box-sizing: border-box;
  }
  .local_query p {
    margin-bottom: 20px;
    text-align: left;
    color: #2E2E30;
    font-size: 20px;
    line-height: 20px;
    font-family: MicrosoftYaHei;
  }
  .local_query ul .large_list {
    margin: 0 0 20px;
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    padding: 30px;
    position: relative;
    font-size: 16px;
    color: #909090;
  }
  .local_query ul .large_list ul li.small_list span:nth-last-child(1) {
    margin-left: 10px;
  }
  .local_query ul .large_list:hover, .local_query ul .large_list:active {
    background: #FFFBF6;
    box-shadow: 0 0 10px 5px rgba(6,0,1,0.1);
  }
  .local_query ul .large_list .collection_btn {
    position: absolute;
    top: 0;
    right: 20px;
    background: url("../../assets/images/search/collectioned.png")no-repeat 100%;
    width: 30px;
    height: 35px;
  }
  .local_query ul .large_list ul .small_list {
    min-width: 400px;
    /*width: 400px;*/
    vertical-align: top;
    display: inline-block;
    margin: 10px;
    overflow: hidden;
  }
</style>



