<template>
  <!--工商企业-->
  <div class="business_enterprise_main page_main">
    <ul>
      <li v-for="item in data" @click="showBEDetails(item.id, item.name)">
        <dl>
          <dt class="title">{{ item.name }}</dt>
          <dd><span>类型</span>: {{ item.type }}</dd>
          <dd><span>法人</span>: {{ item.legalPersonName }}</dd>
          <dd><span>注册时间</span>: {{ item.estiblishTime }}</dd>
          <dd><span>注册资金</span>: {{ item.regCapital }}</dd>
        </dl>
      </li>
    </ul>
  </div>
</template>

<script>
  import bus from '../common/bus'
  // import {mapState, mapActions, mapGetters} from 'vuex'

  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.BUSINESS
      },
      searchKey: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        data: []
      }
    },
    computed: {
    },
    created () {
      gddmgr.commonSearchApi(gconst.SearchType.BUSINESS, { isDemo: false, searchKey: this.searchKey })
        .then(res => {
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          // console.log('工商查询', this.resData)
          // console.log('工商查询', this.data)
          // console.log('工商查询', this.translates)
        })
    },
    methods: {
      showBEDetails (id, name) {
        bus.$emit('createNewTag', {
          searchType: gconst.SearchType.EX_BUSINESS_DETAIL,
          searchKey: id
        })
        bus.$emit('obtNmae', {
          name: name
        })
        // this.$store.dispatch('search/bEDetailsName', name)
      }
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .business_enterprise_main {
    margin: 0 160px 40px;
    padding-bottom: 80px;
  }
  .business_enterprise_main ul {
    overflow: hidden;
    margin: 0 auto;
  }
  .business_enterprise_main ul li {
    width: 420px;
    padding: 30px 50px 0;
    float: left;
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    margin: 15px 10px;
    min-height: 330px;
  }
  .business_enterprise_main ul li dl .title {
    font-size: 20px;
    color: #666666;
    margin-bottom: 30px;
  }
  .business_enterprise_main ul li dl dd {
    margin-bottom: 30px;
    color: #666666;
    font-size: 16px;
  }
  .business_enterprise_main ul li dl dd span {
    font-size: 16px;
    color: #909090;
  }

</style>



