<template>
  <!--Mac地址查询-->
  <div class="mac_query page_main">
    <div class="mac_map">
      <p class="mac_title">Mac地址</p>
      <div class="mac_map_box">
        <!--地图容器-->
        <div class="MacAddressDt" id="MacAddressDt"></div>
      </div>
    </div>
    <div class="mac_information">
      <p class="mac_title">Mac地址详细信息</p>
      <div class="detailed_information">
        <ul>
          <!--<li>查询的Mac： <span>119.168.98.112</span></li>-->
          <li><span>{{ this.translates.country }}</span>： <span>{{ this.data[0].location.address.country }}</span></li>
          <li><span>{{ this.translates.region }}</span>： <span>{{ this.data[0].location.address.region }}</span></li>
          <li><span>{{ this.translates.addressDescription }}</span>： <span>{{ this.data[0].location.addressDescription }}</span></li>
        </ul>
        <ul>
          <li><span>{{ this.translates.city }}</span>： <span>{{ this.data[0].location.address.city }}</span></li>
          <li><span>{{ this.translates.county }}</span>： <span>{{ this.data[0].location.address.county }}</span></li>
          <li><span>{{ this.translates.street }}</span>： <span>{{ this.data[0].location.address.street }}</span></li>
          <li><span>{{ this.translates.accuracy }}</span>： <span>{{ this.data[0].location.accuracy }}</span></li>
        </ul>
        <ul>
          <li><span>{{ this.translates.street_number }}</span>： <span>{{ this.data[0].location.address.street_number }}</span></li>
          <li><span>{{ this.translates.longitude }}</span>： <span>{{ this.data[0].location.longitude }}</span></li>
          <li><span>{{ this.translates.latitude }}</span>： <span>{{ this.data[0].location.latitude }}</span></li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.MAC_ADDRESS
      },
      searchKey: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        data: [],
        translates: {},
        longitude: '',
        latitude: ''
      }
    },
    created () {
      gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
        .then(res => {
          console.log('commonSearchApi NETWORK_TRACES:' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          this.longitude = this.data[0].location.longitude
          this.latitude = this.data[0].location.latitude
          console.log('Mac地址111111', this.resData)
          console.log('Mac地址222222', this.data)
          console.log('Mac地址333333', this.translates)
          console.log('Mac地址查询', this.longitude, this.latitude)

          setTimeout(() => {
            this.loadMap()
          }, 0)
        })
    },
    mounted () {
    },
    methods: {
      loadMap () {
        let map = new BMap.Map('MacAddressDt') //  创建地图实例
        let point = new BMap.Point(this.longitude, this.latitude) // 创建点坐标
        map.centerAndZoom(point, 15) //                          初始化地图 设置中心点坐标和地图级别
        map.enableScrollWheelZoom(true) //                      鼠标滚轮缩放
        let marker = new BMap.Marker(point)
        map.addOverlay(marker) //                                将标注添加到地图上
        marker.setAnimation(BMAP_ANIMATION_BOUNCE) //            跳动的动画

        // 编写自定义函数,创建标注
        function addMarker (point) {
          let marker = new BMap.Marker(point)
          map.addOverlay(marker) //                         将标注添加到地图上
          marker.setAnimation(BMAP_ANIMATION_BOUNCE) //     跳动的动画
        }

        /* 添加工具条、比例尺控件 */
        map.addControl(new BMap.ScaleControl({anchor:BMAP_ANCHOR_TOP_LEFT}))
        map.addControl(new BMap.NavigationControl())
        map.addControl(new BMap.NavigationControl({anchor: BMAP_ANCHOR_BOTTOM_RIGHT, type:BMAP_NAVIGATION_CONTROL_SMALL}))
      },

    }
  }
</script>

<style scoped>
  @import '../search.css';

  .mac_query {

  }
  .mac_query .mac_title {
    font-size:20px;
    font-family:MicrosoftYaHei;
    color:rgba(46,46,48,1);
    line-height:20px;
    margin-bottom: 20px;
    font-weight: bold;
  }
  .mac_query .mac_map .mac_map_box {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    height: 450px;
    margin-bottom: 20px;
    padding: 20px;
  }
  .mac_query .mac_map .mac_map_box .MacAddressDt {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .mac_query .mac_information .detailed_information {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    padding: 0 30px;
    overflow: hidden;
    display: flex;
  }
  .mac_query .mac_information .detailed_information ul {
    margin: 40px;
    flex: 1;
  }
  .mac_query .mac_information .detailed_information li {
    font-size: 16px;
    font-family: MicrosoftYaHei;
    color: rgba(144,144,144,1);
    line-height: 32px;
    min-width: 300px;
  }


</style>



