<template>
  <div class="sidebar">
    <el-menu
      @open="handleOpen"
      @close="handleClose"
      default-active="1"
      class="sidebar-el-menu"
      text-color="#2E2E30">
      <el-submenu v-for="item in searchItems"  index="" :key="item.index">

        <template slot="title">
          <span slot="title">{{ item.title }}</span>
        </template>

        <el-menu-item v-for="subItem in item.subs" @click="itemPosition" index="" :key="subItem.index">
          {{ subItem.title }}
        </el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
  // import bus from '../common/bus'

  export default {
    name: 'sidebar',
    data () {
      return {
        searchItems: [
          {
            index: '',
            title: '',
            subs: [
              {
                index: '',
                title: ''
              }
            ]
          }
        ]
      }
    },
    watch: {
      searchItems: {
        handler (newValue, oldValue) {
          if (newValue !== oldValue) {
            // console.log('@@@@@@@@@@', oldValue)
            // console.log('@@@@@@@@@@', newValue)
          }
        },
        deep: true
      }
    },
    computed: {

    },
    created () {
      this.obtLocalQuery()
    },
    mounted () {

    },
    methods: {
      handleOpen (key, keyPath) {
        // console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        // console.log(key, keyPath)
      },
      itemPosition () {
        console.log(this)
      },
      obtLocalQuery () {
        gapi.getLabelsLabel()
          .then((res) => {
            // console.log('######', res)
            if (res.data.code === 1) {
              this.searchItems = []
              let index = -1
              let lists = res.data.data
              for (let keys1 in lists) {
                index++
                let subIndex = -1
                let itemObj = {}
                itemObj.title = keys1
                itemObj.index = index
                itemObj.subs = []
                for (let keys2 in lists[keys1]) {
                  subIndex++
                  let subsObj = {}
                  subsObj.title = lists[keys1][keys2].label
                  subsObj.index = subIndex
                  itemObj.subs.push(subsObj)
                }
                this.searchItems[index] = itemObj
                this.$set(this.searchItems, index, itemObj)
              }
            } else {
              console.log('未查询到数据或者报错返回搜索主页')
              this.$router.push('/home')
            }
          })
          .catch((res) => {
            console.log('请求失败——返回搜索主页')
            this.$router.push('/home')
          })
      }
    }
  }
</script>

<style>
  .sidebar{
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    box-sizing: border-box;
    overflow-y: scroll;
    height: 100%;
  }
  .sidebar::-webkit-scrollbar{
    width: 0;
  }
  .sidebar > ul {
    height:100%;
    color:rgba(46,46,48,1);
    min-height: 1000px;
    padding-bottom: 300px;
  }
  .sidebar .el-submenu__title {
    background: #ffffff;
  }
  .sidebar .sidebar-el-menu .el-submenu__title span {
    font-size: 18px;
  }
  .sidebar .sidebar-el-menu .el-menu-item {
    color: #666666;
  }
  .sidebar .sidebar-el-menu .el-menu-item:focus, .sidebar .sidebar-el-menu .el-menu-item:hover {
    background: rgba(237,237,237,1);
  }

</style>



