<!-- 通用: 横向布局(一行放4个) -->
<template>
 <div class="common_table_root">
   <div class="resultContainer resultMargin borderRadius">

     <div class="top_bottom">
       <el-row v-for="(row, idx) in getRows" :key="idx" :index="idx">
         <el-col v-for="(col, index) in columns" :key="index" :index="index":span="getSpanWidth">
           <div v-show="getIndex(row, col) < seq.length" class="item">
          <span>
            <span class="key_attr">{{ getKey(row, col) }}</span>
            :
            <common-link-item :k="getENKey(row, col)" :v="getValue(row, col)" :fix="fixAttr.custom"></common-link-item>
            <!--<span class="value_attr">{{ getValue(row, col) }}</span>-->
          </span>
           </div>
         </el-col>
       </el-row>
     </div>


   </div>
 </div>
</template>

<script>
  import CommonLinkItem from '../common/CommonLinkItem.vue'
  export default {
    components: {
      'common-link-item': CommonLinkItem
    },
    props: {
      headers: {
        type: Array,
        default: () => []
      },
      data: {
        type: Object,
        default: () => {}
      },
      columns: {
        type: Number,
        default: 4
      },
      fixAttr: {      // 修正序列
        type: Object,
        default: () => {
          return {
            deletes: [],
            seqs: [],     // 指定顺序，可以指定部分
            custom: {}
          }
        }
      }
    },
    data: function () {
      return {
        seq: ['id', 'ip'],
        translates: {},
      }
    },
    methods: {
      /**
       * 获得当前索引。
       * @param row 从1开始
       * @param col 从1开始
       */
      getIndex (row, col) {
        return (row - 1) * this.columns + (col - 1)
      },

      getENKey (row, col) {
        let idx = this.getIndex(row, col)
        return this.seq[idx]
      },

      getKey (row, col) {
        let idx = this.getIndex(row, col)
        let k = this.seq[idx]
        return this.translates[k]
      },

      getValue (row, col) {
        let idx = this.getIndex(row, col)
        let k = this.seq[idx]
        let v = this.data[k]
        if (typeof v === 'string' && v.trim().length === 0)
          return '-'
        return v
      },
    },
    computed: {
      getSpanWidth () {
        return 24 / ( this.columns || 1)
      },

      getRows () {
        let cnt = this.seq.length
        let cols = this.columns || 1

        let rows = parseInt(cnt / cols)
        if (cnt % cols !== 0)
          rows += 1

        return rows
      }
    },
    created () {
      if (this.headers.length > 0) {
        const { seq, translates } = gfuncs.convertHeaderToSeqAndTranslates(this.headers)

        this.seq = gfuncs.fixSeqs(seq, this.fixAttr)
        this.translates = translates
      }
    }
  }
</script>


<style>
  @import "../search.css";

  .common_table_root {
    margin-top: 20px;
  }

  .borderRadius {
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    padding: 0;
  }

  .top_bottom {
    margin: 20px 10px 20px 10px;
    /*background: green;*/
  }

  .item {
    /*background: red;*/
    margin: 10px 10px;
  }

</style>



