<!--QQ群查询： 通过QQ群ID查询-->
<template>
  <div>
    <div class="resultTitle">QQ群基本信息</div>
    <common-table :headers="props.qunBaseHeaders" :datas="[props.qunBaseData]" :fixAttr="fixBaseSeqAttr"></common-table>

    <div class="resultTitle">QQ群成员列表</div>
    <common-column-groups :headers="getMembersHeaders" :datas="getMembersDatas" :fixAttr="fixAttr"></common-column-groups>
  </div>
</template>

<script>
  import CommonTable from '../../common/CommonTable.vue'
  import CommonColumnGroups from '../../common/CommonColumnGroups.vue'
  export default {
    name: 'QQMatch',
    components: {
      'common-table': CommonTable,
      'common-column-groups': CommonColumnGroups
    },
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.QQ_SEARCH
      },
      searchKey: {
        type: String,
        default: ''
      },
      props: {
        type: Object,
        default: () => {
          return {
            qunBaseData: {},
            qunBaseHeaders: [],
            qunData: {},
          }
        }
      }
    },
    data () {
      return {
        qunDatas: [],
        fixBaseSeqAttr: {
          deletes: [],
          seqs: ['qunNum', 'title', 'qunText'],
          custom: {
            'qunNum': {
              linkType: gconst.LinkType.QQQun
            },
          }
        },
        fixAttr: {
          deletes: ['qunNum', 'title', 'qunText'],
          seqs: [],
          custom: {
            'qqNum': {
              linkType: gconst.LinkType.QQ
            },
          }
        },
      }
    },

    computed: {
      getMembersHeaders () {
        if (this.props.qunData && this.props.qunData.data) {
          return this.props.qunData.data.headers
        }

        return []
      },

      getMembersDatas () {
        if (this.props.qunData && this.props.qunData.data) {
          return this.props.qunData.data.resultList
        }

        return []
      }
    },

    created () {
      if (this.props.qunData && this.props.qunData.data) {
        this.qunHeaders = this.props.qunData.data.headers
        this.qunDatas = this.props.qunData.data.resultList
      }
    }
  }
</script>

<style scoped>
  @import '../../search.css';

</style>



