<script>
  import bus from '../common/bus'

  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.EX_BUSINESS_DETAIL
      },
      searchKey: {
        type: Number,
        default: ''
      }
    },
    data () {
      return {
        tableData: [
          {
            name: '王小虎',
            capital: '王大放送法撒旦发送到小虎',
            time: '2016-05-02',
            state: '发送到风随碟附送'
          }
        ],
        // 1——经营异常
        // 2——行政处罚
        // 3——分支机构
        // 4——变更记录
        changereCord: [
          {
            changeItem: '',
            createTime: '',
            contentBefore: '',
            contentAfter: '',
            changeTime: ''
          }
        ],
        changereCordTotalRowNum: 1,
        changereCordscrollId: 1,
        name: '',
        // 5——失信人
        // 6——全维度数据查询
        ObtainNumber: ''
        // 7——法律诉讼
        // 8——对外投资
        // 9——被执行人
        // 10——严重违法
        // 11——抽查检查
        // 12——欠税公告
        // 13——税务评级
        // 14——网站备案
      }
    },
    watch: {
      name: function (value, oldValue) {
        // console.log(value, '2222', oldValue)
      }
    },
    created () {
      bus.$on('obtNmae', msg => {
        this.name = msg.name
        this.obtDetailsData()
      })
    },
    methods: {
      obtDetailsData () {
        this.obtAbnormal() // 经营异常
        this.obtAdministrat() // 行政处罚
        this.obtBranchme() // 分支机构
        this.obtainChangeRecode() // 变更记录
        this.obtDishonestPerson() // 5——失信人
        this.obtFullDimension() // 6——全维度数据查询
        this.obtLegalproceed() // 7——法律诉讼
        this.obtOutBound() // 8——对外投资
        this.obtPersonSubJected() // 9——被执行人
        this.obtEriousvio() // 10——严重违法
        this.obtPotcheck() // 11——抽查检查
        this.obtTaxnotice() // 12——欠税公告
        this.obtTaxrating() // 13——税务评级
        this.obtWebsitefiling() // 14——网站备案
      },
      // 1 —— 经营异常
      obtAbnormal () {
        let params1 = {
          id: this.searchKey
        }
        gapi.getBusinessAbnormal(params1).then(res => {
          console.log('##经营异常##' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          console.log('工商企业详情', this.resData)
          console.log('工商企业详情', this.data)
          console.log('工商企业详情', this.translates)
        })
      },
      // 2 —— 行政处罚
      obtAdministrat () {
        let params2 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessAdministrat(params2).then(res => {
          console.log('##行政处罚##', res)
        })
      },
      // 3——分支机构
      obtBranchme () {
        let params3 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessBranchme(params3).then(res => {
          console.log('##分支机构##', res)
        })
      },
      // 4 —— 变更记录
      obtainChangeRecode () {
        let params4 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: this.changereCordscrollId
        }
        gapi.getBusinessChangereCord(params4).then(res => {
          console.log('##变更记录##', res)
          this.changereCord = res.data.data.resultList
          this.changereCordTotalRowNum = res.data.data.totalRowNum
        })
      },
      // 5——失信人
      obtDishonestPerson () {
        let params5 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessDishonestPerson(params5).then(res => {
          console.log('##失信人##', res)
        })
      },
      // 6——全维度数据查询
      obtFullDimension () {
        let params6 = {
          id: this.searchKey,
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        console.log('！！！全部参数', params6)
        gapi.getBusinessfulldimension(params6).then(res => {
          console.log('##全维度数据查询##', res)
          this.fulldimension = res.data.data.result
          this.EssentialInformation = this.fulldimension.EssentialInformation
          this.ObtainNumber = this.fulldimension.ObtainNumber
          this.itemK = this.EssentialInformation.headers
          console.log('11111', this.fulldimension, this.EssentialInformation, this.itemK)
          for (let j in this.itemK) {
            this.FullDimensionAllKData = this.itemK[j].fieldCH
            console.log(this.FullDimensionAllKData)
          }
        })
      },
      // 7——法律诉讼
      obtLegalproceed () {
        let params7 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessLegalproceed(params7).then(res => {
          console.log('##法律诉讼##', res)
        })
      },
      // 8——对外投资
      obtOutBound () {
        let params8 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessOutBound(params8).then(res => {
          console.log('##对外投资##', res)
        })
      },
      // 9——被执行人
      obtPersonSubJected () {
        let params9 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessPersonSubJected(params9).then(res => {
          console.log('##被执行人##', res)
        })
      },
      // 10——严重违法
      obtEriousvio () {
        let params10 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinesssEriousvio(params10).then(res => {
          console.log('##严重违法##', res)
        })
      },
      // 11——抽查检查
      obtPotcheck () {
        let params11 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinesssPotcheck(params11).then(res => {
          console.log('##抽查检查##', res)
        })
      },
      // 12——欠税公告
      obtTaxnotice () {
        let params12 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessTaxnotice(params12).then(res => {
          console.log('##欠税公告##', res)
        })
      },
      // 13——税务评级
      obtTaxrating () {
        let params13 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessTaxrating(params13).then(res => {
          console.log('##税务评级##', res)
        })
      },
      // 14——网站备案
      obtWebsitefiling () {
        let params14 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessWebsitefiling(params14).then(res => {
          console.log('##网站备案##', res)
        })
      },

      handleCurrentChange (val) { // 变更记录
        this.changereCordTotalRowNum = val
        this.obtainChangeRecode()
      }
    }
  }
</script>

