<template>
  <!--互联网轨迹-->
  <div class='internet_track page_main'>
    <div class='register'>
      <el-button>全部</el-button>
      <el-button>未注册</el-button>
      <el-button>已注册</el-button>
    </div>
    <div class='internet_track_content'>
      <ul>
        <li v-for ='item in data'>
          <div class='header_img'><img class='img' :src='item.imgUrl'></div>
          <dl class='details'>
            <dt>
              <p class='name_title'>{{ item.webName }}</p>
              <p>是否注册：<span class='mark'>{{ item.flag ? '已注册' : '未注册' }}</span></p>
              <p>网址：<span class='website'><a target="_blank" :href="'http://' + item.webSite">{{ item.webSite }}</a></span></p>
            </dt>
            <dd>简介：<span>{{ item.briefIntroduction }}</span></dd>
          </dl>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
      name: '互联网轨迹',
      props: {
        searchType: {
          type: Number,
          default: gconst.SearchType.NETWORK_TRACES
        },
        searchKey: {
          type: String,
          default: ''
        }
      },
      data () {
        return {
          data: [],
          translates: {}
        }
      },
      created () {
        // alert('this.sarchKey:' + this.searchKey + ' type:' + this.searchType)
        gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
          .then(res => {
            console.log('commonSearchApi NETWORK_TRACES:' + JSON.stringify(res))
            this.resData = res.resData
            this.data = res.resData.rawData
            this.translates = this.resData.translates || {}
            // console.log('互联网轨迹', this.resData)
            // console.log('互联网轨迹', this.data)
            // console.log('互联网轨迹', this.translates)
          })
      }
    }
</script>

<style scoped>
  @import '../search.css';

  .internet_track {
    position: relative;
    max-width: 1600px;
    margin: 0 160px;
    overflow-y: scroll;
    height: 100%;
  }
  .internet_track .register {
    position: absolute;
    top: 30px;
    right: 30px;
  }
  .internet_track .register button {
    width: 60px;
    height: 40px;
    margin: 0 15px;
    background: rgba(167,177,194,1);
    border-radius: 5px;
    border: none;
    cursor: pointer;
    text-align: center;
    padding: 0;
    color: rgba(255,255,255,1);
    font-size: 16px;
    line-height: 40px;
  }
  .internet_track .register button:hover {
    background: rgba(7,134,223,1);
  }
  .internet_track .internet_track_content {
    margin: 80px auto 0;
  }
  .internet_track .internet_track_content li {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    padding: 30px;
    margin-bottom: 20px;
  }
  .internet_track .internet_track_content li .header_img {
    width: 72px;
    height: 72px;
    margin-right: 30px;
    display: inline-block;
  }
  .internet_track .internet_track_content li .header_img img {
    width: 100%;
    height: 100%;
    display: block;
  }
  .internet_track .internet_track_content li .details {
    display: inline-block;
    vertical-align: top;
    width: 90%;
  }
  .internet_track .internet_track_content li .details dt {
    margin-bottom: 20px;
  }
  .internet_track .internet_track_content li .details dt p {
    display: inline-block;
    margin-right: 60px;
    color: #909090;
    font-size: 16px;
  }
  .internet_track .internet_track_content li .details dt .name_title {
    font-size: 20px;
    color: #666666;
    font-weight: bold;
  }
  .internet_track .internet_track_content li .details dt .mark {
    color: #53BD6D;
    font-weight: bold;
  }
  .internet_track .internet_track_content li .details dt .website {
    color: #3B97D4;
    font-weight: bold;
  }
  .internet_track .internet_track_content li .details dd {
    color: rgba(102,102,102,1);
    font-size: 16px;
    line-height: 30px;
  }
  .internet_track .internet_track_content li .details dd span {
    color: #909090;
    font-size: 16px;
    margin-left: 6px;
  }
</style>



