<!-- 银行卡查询 -->
<template>
  <div>
    <div v-for="(item, index) in keys" :index="index" :key="index">
      <common-table :headers="result[item].headers" :datas="result[item].data"></common-table>
    </div>
  </div>
</template>

<script>
  import CommonTable from '../common/CommonTable.vue'
  import bus from '../common/bus'
  import DDBankData from '../../data/search_data/DDBankPlus'

  export default {
    components: {
      'common-table': CommonTable
    },

    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.WEIBO
      },
      searchKey: {
        type: String,
        default: '成都'
      },
      isDemo: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        seq: [],
        datas: [],
        data: [],
        translates: {},
        keys: [],         // 渲染的顶层列表
        result: {},
        res_data: {
          headers: [],
          data: []
        }
      }
    },
    methods: {
      getHeader (key) {
        return this.result[key]
      },

      showDetail (id) {
        bus.$emit('createNewTag', {
          searchType: gconst.SearchType.EX_WEIBO_DETAIL,
          searchKey: id
        })
      }
    },
    created () {
      let res_data = {
        "headers":[
          {
            "fieldEN":"merchantName",
            "fieldCH":"商户名称"
          },
          {
            "fieldEN":"address",
            "fieldCH":"地点"
          },
          {
            "fieldEN":"money",
            "fieldCH":"交易金额"
          },
          {
            "fieldEN":"goods",
            "fieldCH":"物品"
          },
          {
            "fieldEN":"transactionTime",
            "fieldCH":"时间"
          }
        ],
        "data":[
          {
            "address":"成都市",
            "money":"6000.00",
            "goods":"1520",
            "transactionTime":"20170721",
            "merchantName":"成都市嘉锦置业有限公司"
          },
          {
            "address":"四川省",
            "money":"70.00",
            "goods":"5411",
            "transactionTime":"20170721",
            "merchantName":"重庆家乐福八宝店"
          }]
      }
      this.res_data = res_data

      if (this.isDemo) {
        let demo_data = gddmgr.getSearchDemoData(this.searchType, false)
        this.result = demo_data.data.result
      } else {
        gddmgr.searchRequest(this.searchType, this.searchKey, false, false).then(res => {
          console.log("bank data:" + JSON.stringify(res))

          this.result = res.data.result
          this.keys = Object.keys(this.result)
        }).catch( err => {
          console.log("bank catch error:" + JSON.stringify(err))
        })
      }






//      gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
//        .then(res => {
//          console.log('commonSearchApi:' + JSON.stringify(res))
//          this.resData = res.resData
//          this.data = res.resData.rawData
//          this.translates = this.resData.translates || {}
//        })
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .weibo_list_main {
    overflow: hidden;
  }
  .weibo_list_main ul {
    overflow: hidden;
    display: flex;
    display: -webkit-flex; /* Safari */
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .weibo_list_main ul li {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 0 rgba(6,0,1,0.1);
    margin: 15px 10px;
    padding: 20px;
    max-width: 480px;
  }
  .weibo_list_main ul li .header_img {
    display: inline-block;
    vertical-align: middle;
    background: #3F4262;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 30px;
  }

  .img {
    width: 100px;
    height: 100px;
  }

  .weibo_list_main ul li dl {
    display: inline-block;
    vertical-align: middle;
    max-width: 345px;
  }
  .weibo_list_main ul li dl .name_title {
    color: #666666;
    font-size: 20px;
    margin-bottom: 20px;
  }
  .weibo_list_main ul li dl dd {
    margin-bottom: 20px;
    font-size: 16px;
    color: #666666;
  }
  .weibo_list_main ul li dl dd span {
    color: #909090;
    font-size: 16px;
  }
</style>



