<!--
通用: 显示结果table部分
通过seq来控制所有的显示
-->
<template>
 <div class="common_table_root">
   <div class="resultContainer resultMargin borderRadius">
     <el-table class="common_table" :data="datas" border style="width:100%" ref="logsTable">
       <el-table-column v-for="(k, index) in seq" :key="index" :prop="k" :label="translates[k]" align="center">
         <template slot-scope="scope">
           <common-link-item :k="k" :v="scope.row[k]" :fix="fixAttr.custom"></common-link-item>
         </template>
       </el-table-column>
     </el-table>
   </div>
 </div>
</template>

<script>
  import CommonLinkItem from '../common/CommonLinkItem.vue'
  export default {
    components: {
      'common-link-item': CommonLinkItem
    },

    props: {
      headers: {
        type: Array,
        default: () => []
      },
      datas: {
        type: Array,
        default: () => []
      },
      fixAttr: {      // 修正序列
        type: Object,
        default: () => {
          return {
            deletes: [],
            seqs: [],     // 指定顺序，可以指定部分
            custom: {}
          }
        }
      }
    },
    data: function () {
      return {
        seq: ['id', 'ip'],
        translates: {}
      }
    },
    methods: {
    },
    computed: {

    },
    created () {
      if (this.headers.length > 0) {
        const { seq, translates } = gfuncs.convertHeaderToSeqAndTranslates(this.headers)
        this.seq = gfuncs.fixSeqs(seq, this.fixAttr)
        this.translates = translates
      }
    }
  }
</script>


<style>
  @import "../search.css";

  .common_table_root {
    margin-top: 20px;
  }

  .common_table_root .el-table .el-table__header .cell {
    /*height: auto;       <!-- 覆盖属性 -->*/
    /*line-height: 50px;*/
  }

  .common_table_root .el-table .el-table__body .cell {
    height: auto;
    /*line-height: 40px;*/
  }

  .borderRadius {
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    border-radius: 50px;
    padding: 0;
  }

</style>



