<!-- 通用: 横向布局(一行放4个) -->
<template>
 <div>
   <div v-for="(data, index) in datas" :key="index" :index="index">
     <common-column-group :headers="headers" :fixAttr="fixAttr" :columns="columns" :data="data"></common-column-group>
   </div>
 </div>
</template>

<script>
  import CommonColumnGroup from './CommonColumnGroup.vue'
  export default {
    components: {
      'common-column-group': CommonColumnGroup
    },
    props: {
      headers: {
        type: Array,
        default: () => []
      },
      datas: {
        type: Array,
        default: () => []
      },
      columns: {
        type: Number,
        default: 4
      },
      fixAttr: {      // 修正序列
        type: Object,
        default: () => {
          return {
            deletes: [],
            seqs: [],     // 指定顺序，可以指定部分
            custom: {}
          }
        }
      }
    },
    data: function () {
      return {
        seq: ['id', 'ip'],
        translates: {},
      }
    },
    methods: {
    },
    computed: {

    },
    created () {

    }
  }
</script>


<style>


</style>



