<!--QQ查询-->
<template>
  <div>
    <div class="resultTitle">QQ相关信息</div>
    <common-table :headers="baseHeaders" :datas="baseDatas" :fixAttr="fixBaseSeqAttr"></common-table>

    <div class="resultTitle">QQ相关群信息</div>
    <common-column-groups :headers="qunHeaders" :datas="qunDatas" :fixAttr="fixSeqAttr"></common-column-groups>
  </div>
</template>

<script>
  import CommonTable from '../../common/CommonTable.vue'
  import CommonColumnGroups from '../../common/CommonColumnGroups.vue'
  export default {
    name: 'QQMatch',
    components: {
      'common-table': CommonTable,
      'common-column-groups': CommonColumnGroups
    },
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.QQ_SEARCH
      },
      searchKey: {
        type: String,
        default: ''
      },
      props: {
        type: Object,
        default: () => {}
      }
    },
    data () {
      return {
        baseHeaders: [],
        baseDatas: [],
        qunDatas: [],
        qunHeaders: [],
        fixBaseSeqAttr: {
          deletes: ['ipAddress', 'netDate'],
          seqs: ['qqNum', 'ip'],
          custom: {
            'qqNum': {
              linkType: gconst.LinkType.QQ
            },
            'ip': {
              linkType: gconst.LinkType.IP
            },
          }
        },
        fixSeqAttr: {
          deletes: [],
          seqs: ['nick', 'qunNum', 'title', 'createDate'],
          custom: {
            'qunNum': {
              linkType: gconst.LinkType.QQQun
            },
          }
        },
      }
    },

    computed: {
    },

    created () {
      let src_data = this.props.data
      if (src_data.data) {
        let qqs = src_data.data.qqs || {}
        let quns = src_data.data.quns || {}

        this.baseDatas = qqs.resultList
        this.baseHeaders = qqs.headers

        this.qunHeaders = quns.headers
        this.qunDatas = quns.resultList
      }
    }
  }
</script>

<style scoped>
  @import '../../search.css';

</style>



