<template>
  <div class="content-box">
    <div class="tags">
      <ul class="tags_ul">
        <li class="tags-li" v-for="(tag, idx) in getTags" :class="{'active': isActive(idx)}" :key="idx" @click="activeTag(tag, idx)">
         <span class="tag-container">
            <span>{{ getTagName(tag) }}</span>
            <span>{{ getTagKey(tag) }}</span>
          </span>
          <span class="tags-li-icon" @click.stop="closeTag(tag, idx)"><i class="el-icon-close"></i></span>
        </li>
      </ul>
      <div class="tags-close-box">
        <el-dropdown @command="handleTags">
          <el-button size="mini" type="primary">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu size="small" slot="dropdown">
            <el-dropdown-item command="other">关闭其他</el-dropdown-item>
            <el-dropdown-item command="all">关闭所有</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>

    <div class="content">
      <transition name="move" mode="out-in">
        <keep-alive>
          <div class="tag-content" ref="container"></div>
        </keep-alive>
      </transition>
    </div>
  </div>
</template>

<script>
  import bus from '../common/bus'
  import Vue from 'vue'
  import ThirdView from '../common/CommonThirdView.vue'
  import WeiBoList from '../page/WeiBoList.vue'
  import NetworkTraces from '../page/NetworkTraces.vue'
  import BusinessEnterprise from '../page/BusinessEnterprise.vue'
  import BusinessEnterpriseDetails from '../page/BusinessEnterpriseDetails'
  import LocalQuery from '../page/LocalQuery.vue'
  import IPPlus from '../page/IPAddressExactQuery.vue'
  import MacSearch from '../page/MacAddressQuery.vue'
  import Facebook from '../page/Facebook.vue'
  import Twitter from '../page/Twitter.vue'
  import WeiBoDetails from '../page/WeiBoDetails'
  import Bank from '../page/Bank.vue'
  import QQ from '../page/QQ.vue'

  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        tagsList: [],
        tagComponents: {}
      }
    },
    created () {
      // console.log("---------tag created")
    },
    activated () {
      console.log('this.router.parsm:' + this.$route.params)
      if (this.$route.params) {
        const { searchKey, searchType } = this.$route.params
        if (searchType !== undefined && searchType !== null) {
          this.createNewTag(this.$route.params)
        }
      }
    },
    deactivated () {
      // console.log("---------tag deactivated")
    },
    mounted () {
      let tags = this.getTags
      if (tags.length > this.tagsList.length && this.tagsList.length === 0) {
        for (let tag of tags) {
          let arr = gfuncs.splitSearchTag(tag)
          this.createNewTag({
            searchType: parseInt(arr[0]),
            searchKey: arr[1]
          })
        }
      }

      bus.$on('createNewTag', params => {
        // console.log('------------------createNewTag on')
        this.createNewTag(params)
      })

    },
    methods: {
      getTagType (tag) {
        return tag.params.searchType
      },
      // 获得对应的组件
      getViewComponent (searchType) {
        switch (searchType) {
          case gconst.SearchType.SUPER_SEARCH:
            return LocalQuery
          case gconst.SearchType.QQ_SEARCH:
            return QQ
          case gconst.SearchType.IP_PLUS:
            return IPPlus
          case gconst.SearchType.MAC_ADDRESS:
            return MacSearch
          case gconst.SearchType.WEIBO:
            return WeiBoList
          case gconst.SearchType.EX_WEIBO_DETAIL:
            return WeiBoDetails
          case gconst.SearchType.FACEBOOK:
            return Facebook
          case gconst.SearchType.TWITTER:
            return Twitter
          case gconst.SearchType.NETWORK_TRACES:
            return NetworkTraces
          case gconst.SearchType.BUSINESS:
            return BusinessEnterprise
          case gconst.SearchType.EX_BUSINESS_DETAIL:
            return BusinessEnterpriseDetails
          case gconst.SearchType.BANK_PLUS:
            return Bank
          case gconst.SearchType.PHONE_STATE:
          case gconst.SearchType.IP:
          default:
            return ThirdView
        }
      },

      // 获得标签的名字
      getTagNameByType (searchType) {
        let title = gddmgr.getScreenTitle(searchType)
        return title
      },

      // Tag的类型
      getTagName (tag) {
        let params = gfuncs.splitSearchTag(tag)
        let type = parseInt(params[0] || '0')
        return this.getTagNameByType(type)
      },

      getTagKey (tag) {
        let params = gfuncs.splitSearchTag(tag)
        if (params && params.length > 1)
          return params[1]

        return ''
      },

      isActive (index) {
        return this.getActiveIndex === index
      },

      createNewTag (params) {
        this.hideLastTag()

        let tag = gfuncs.geneSearchTag(params.searchType, params.searchKey)

        // 内部可能回更改activeIndex
        this.$store.dispatch('tags/addTag', {
          searchType: params.searchType,
          searchKey: params.searchKey
        })

        if (this.tagComponents[tag]) {
          let tc = this.tagComponents[tag]
          tc && tc.instance.$el.removeAttribute('hidden')
        } else {
          let component = this.getViewComponent(params.searchType)
          let ComponentClass = Vue.extend(component)
          let instance = new ComponentClass({
            propsData: {
              searchType: params.searchType,
              searchKey: params.searchKey,
              isDemo: params.isDemo
            }
          })
          instance.$mount() // pass nothing
          this.$refs.container.appendChild(instance.$el)

          this.tagComponents[tag] = {
            searchType: params.searchType,
            searchKey: params.searchKey,
            instance: instance
          }
        }
      },

      hideLastTag () {
        let lastIdx = this.getActiveIndex
        let oldTagName = this.$store.getters['tags/getTag'](lastIdx)

        if (lastIdx >= 0 && lastIdx < this.getTags.length) {
          let tag = this.tagComponents[oldTagName]
          tag && tag.instance.$el.setAttribute('hidden', 'true')
        }
      },

      // 激活当前tag
      activeTag (tagName, index) {
        let lastIdx = this.getActiveIndex
        if (lastIdx >= 0) {
          this.hideLastTag()

          this.$store.dispatch('tags/setActiveIndex', index)

          let tag = this.tagComponents[tagName]
          tag && tag.instance.$el.removeAttribute('hidden')
        }
      },

      // 关闭单个标签
      closeTag (tagName, index) {
        let oldTagName = this.$store.getters['tags/getTag'](index)

        let oldTag = this.tagComponents[oldTagName]
        this.releaseTag(oldTag, oldTagName)

        this.$store.dispatch('tags/closeTag', index)

        if (this.getActiveIndex >= 0 && this.getTags.length >= 0) {
          let lastIdx = this.getActiveIndex
          let newTagName = this.$store.getters['tags/getTag'](lastIdx)

          let tag = this.tagComponents[newTagName]
          tag && tag.instance.$el.removeAttribute('hidden')
        }
      },

      // 释放Tag
      releaseTag (tagItem, tagName) {
        if (tagItem && tagItem.instance) {
          let instance = tagItem.instance
          instance.$destroy()
          this.$refs.container.removeChild(instance.$el)

          delete this.tagComponents[tagName]
        }
      },

      // 关闭其他标签
      closeOther () {
        let idx = this.getActiveIndex
        let activeTagName = this.$store.getters['tags/getTag'](idx)
        let activeTag = this.tagComponents[activeTagName]
        for (let tagName in this.tagComponents) {
          if (tagName != activeTagName) {
            let tag = this.tagComponents[tagName]
            this.releaseTag(tag, tagName)
          }
        }
        this.tagComponents[activeTagName] = activeTag
        this.$store.dispatch('tags/closeOtherTags')
      },

      // 关闭全部标签
      closeAll () {
        for (let tagName in this.tagComponents) {
          let tag = this.tagComponents[tagName]
          this.releaseTag(tag, tagName)
        }

        this.tagComponents = {}

        this.$store.dispatch('tags/closeAllTags')
      },

      handleTags (command) {
        command === 'other' ? this.closeOther() : this.closeAll()
      }
    },
    computed: {
      ...mapGetters({
        getTags: 'tags/getTags',
        getActiveIndex: 'tags/getActiveIndex'
      })
    }
  }
</script>

<style>
  .content-box .tags {
    margin: 0 auto;
    width: 100%;
    height: 50px;
    background: #6B9DFB;
  }
  .tag-container {
    /*display: flex;*/
    flex-direction: column;
    align-items: center;
    display: inline-flex;
  }
  .tags-li-icon {
    margin-left: 10px;
  }
  .content-box .tags_ul {
    box-sizing: border-box;
    overflow: hidden;
    float: left;
    width: calc(100% - 51px);
  }
  .content-box .tags-close-box {
    float: left;
  }
  .content-box .tags-close-box .el-dropdown button {
    height: 50px;
  }
  .tags-li {
    float: left;
    margin: 5px;
    border-radius: 3px;
    font-size: 12px;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid #FFFFFF;
    background-color: #6B9DFB;
    padding: 3px 5px 3px 12px;
    vertical-align: middle;
    color: #FFFFFF;
    -webkit-transition: all .3s ease-in;
    -moz-transition: all .3s ease-in;
    transition: all .3s ease-in;
    display: flex;
    align-items: center;
  }
  .tags-li:not(.active):hover {
    background: #f8f8f8;
    color: #4778C7;
    border: 1px solid #e9eaec;
  }
  .tags-li.active {
    border: 1px solid #e9eaec;
    background: #fff;
    color: #4778C7;
  }
  .tags-li-title {
    float: left;
    max-width: 80px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-right: 5px;
    color: #666;
  }
  .tags-li.active .tags-li-title {
    color: #fff;
  }
  .content-box .content {
    margin: 0 auto;
    width: 100%;
    padding: 0;
    background: rgba(240,240,240,1);
    position: relative;
    height: calc(100% - 50px);
  }
  .tag-content {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow-y: auto;
    overflow-x: hidden;
    box-sizing: border-box;
  }
</style>



