<template>
  <!-- Twitter -->
  <div class="weibo_list_main page_main">
    <ul>
      <li v-for="item in data">
        <div class="header_img"><img class="img" :src="item.avatarUrl"></div>
        <dl>
          <dt class="name_title">{{ item.userName }}</dt>
          <dd><span>用户的ID:</span>{{ item.id }}</dd>
          <dd><span>用户的用户名:</span>{{ item.userName }}</dd>
          <dd><span>用户显示的名称:</span>{{ item.screenName }}</dd>
          <dd><span>点赞数:</span>{{ item.likeCount }}</dd>
          <dd><span>粉丝数，既被关注数:</span>{{ item.fansCount }}</dd>
          <dd><span>关注数:</span>{{ item.followCount }}</dd>
          <dd><span>用户所在位置:</span>{{ item.location }}</dd>
          <dd><span>账号类型：个人的、公众号:</span>{{ item.idType }}</dd>
          <dd><span>账号是否验证:</span>{{ item.idVerified }}</dd>
          <dd><span>本对象的最通用url:</span><a target="_blank" :href="item.url">{{ item.url }}</a></dd>
          <dd><span>账号验证信息:</span>{{ item.idVerifiedInfo }}</dd>
          <dd><span>简单自传:</span>{{ item.biography }}</dd>
        </dl>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Twitter',
  props: {
    searchType: {
      type: Number,
      default: gconst.SearchType.TWITTER
    },
    searchKey: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      resData: {},
      data: [],
      translates: {}
    }
  },
  methods: {
  },
  created () {
    gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
      .then(res => {
        console.log('commonSearchApi:' + JSON.stringify(res))
        this.resData = res.resData
        this.data = res.resData.rawData
        this.translates = this.resData.translates || {}
        console.log('Twitter', this.resData)
        console.log('Twitter', this.data)
        console.log('Twitter', this.translates)
      })
  }
}
</script>

<style scoped>
  @import '../search.css';

  .weibo_list_main {
    overflow: hidden;
  }
  .weibo_list_main ul {
    overflow: hidden;
    display: flex;
    display: -webkit-flex; /* Safari */
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .weibo_list_main ul li {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 0 rgba(6,0,1,0.1);
    margin: 15px 10px;
    padding: 20px;
    position: relative;
    width: 100%;
  }
  .weibo_list_main ul li .header_img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .weibo_list_main ul li .header_img .img {
    width: 100px;
    height: 100px;
    display: block;
  }
  .weibo_list_main ul li dl {
    display: inline-block;
    vertical-align: middle;
    margin-left: 150px;
  }
  .weibo_list_main ul li dl .name_title {
    color: #666666;
    font-size: 20px;
    margin-bottom: 20px;
  }
  .weibo_list_main ul li dl dd {
    margin-bottom: 20px;
    font-size: 16px;
    color: #666666;
    line-height: 32px;
    float: left;
    min-width: 400px;
    margin-right: 20px;
  }
  .weibo_list_main ul li dl dd span {
    color: #909090;
    font-size: 16px;
    margin-right: 6px;
  }
</style>



